export default {
  data: function data() {
    return {};
  },
  props: {
    node: {}
  },
  methods: {
    handleAddNode: function handleAddNode(data) {
      var sort = data.children.length + 1;
      var node = {
        id: new Date().getTime(),
        name: "",
        parentId: data.id,
        sort: sort
      };
      data.children.push(node); // this.$emit('updateCatalogList')
    },
    handleDeleteNode: function handleDeleteNode(node) {
      var _this = this;

      if (node.parentId) {
        var parentNode = this.$parent.cataList.find(function (el) {
          return el.id === node.parentId;
        });
        parentNode.children.splice(node.sort - 1, 1);
        this.$emit('updateCatalogue');
      }

      if (node.name) {
        this.$http.post(this.INTERFACE.deleteCatalogue, {
          'id': node.id
        }).then(function (res) {
          if (res.data.code == 200) {
            _this.$parent.getCatalogueList();
          }
        });
      }
    } // updateCatalogList(node){
    //     this.$parent.catalogueList.find(el=>el.id === node.id)
    // }

  }
};